import { Component, OnInit } from '@angular/core';
import { Unsubscribe } from 'redux';
import { ProductModel } from 'src/app/models/product.model';
import store from 'src/app/redux/store';
import { ProductsService } from 'src/app/services/product.service';
import { environment } from 'src/environments/environment';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModalService } from 'src/app/services/modal.service';

@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css'],
})
export class AdminPanelComponent implements OnInit {
  public product = new ProductModel();
  public productToUpdate: ProductModel;
  public categories: ProductModel[];
  public imageUrl: string;
  public thisOpen: boolean = false;
  public addFormOpen: boolean = true;
  public toStingFlag: boolean = false;
  public preview: string; // Image preview
  public unsubscribeStore: Unsubscribe;
  public isUpdated: boolean = false;
  public isAdded: boolean = false;
  public panelOpenState = false;
  public form: FormGroup;
  public timer:ReturnType<typeof setTimeout>;;
  

  constructor(
    private myProductService: ProductsService,
    private myModalService:ModalService
  ) {}

  requiredError = new FormControl('', [Validators.required]);

  public async ngOnInit():Promise<void> {
      
    this.unsubscribeStore = store.subscribe(() => {
      this.productToUpdate = store.getState().productsState.productToUpdate[
        store.getState().productsState.productToUpdate.length - 1
      ];

      if (this.productToUpdate) {
        this.thisOpen = true;
        this.imageUrl =
          environment.productsUrl +
          'images/' +
          this.productToUpdate.imageFileName;
        if (typeof this.productToUpdate.categoryId == 'string') {
          this.toStingFlag = true;
        } else {
          this.toStingFlag = false;
        }
      }
      
    });

    this.categories = await this.myProductService.getAllCategories();
  }

  public reset():void{
    this.productToUpdate = store.getState().productsState.productToUpdate[
        store.getState().productsState.productToUpdate.length - 1
      ];
  }

  public handleImage(image: Event): void {
    this.product.image = (image.target as HTMLInputElement).files[0];
    // Read the image into preview variable:
    const myFileReader = new FileReader(); // JavaScript object which can read files from the user computer
    myFileReader.onload = (args) =>
      (this.preview = args.target.result.toString()); // When complete reading - set the image into the preview variable
    myFileReader.readAsDataURL(this.product.image);
  }

  public handleImageUpdate(image: Event): void {
    this.productToUpdate.image = (image.target as HTMLInputElement).files[0];
    // Read the image into preview variable:
    const myFileReader = new FileReader(); // JavaScript object which can read files from the user computer
    myFileReader.onload = (args) =>
      (this.preview = args.target.result.toString()); // When complete reading - set the image into the preview variable
    myFileReader.readAsDataURL(this.productToUpdate.image);
  }

  public async addProduct():Promise<void> {
    try {
      await this.myProductService.addProduct(this.product);
      this.preview = '';
      this.isAdded = true;
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal()
      }
      if (err.status === 401) {
        this.myModalService.openAdminOverModal()
      }
      console.log(err);
    }
    this.timer = setTimeout(() => {
      this.isAdded = false;
      this.thisOpen = false
    }, 2000);
  }

  public async editProduct():Promise<void> {
    try {
      await this.myProductService.editProduct(this.productToUpdate);
      this.isUpdated = true;
      this.preview = ''
      this.imageUrl = '';
    
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal()
      }
          if (err.status === 401) {
        this.myModalService.openAdminOverModal()
      }
      console.log(err.error);
    }
    this.timer =setTimeout(() => {
      this.isUpdated = false;
      this.thisOpen = false
    }, 2000);
  }

  public cancel():void {
    this.preview = '';
    this.thisOpen = false
    this.addFormOpen = false
  }
  public ngOnDestroy(): void {
    this.unsubscribeStore && this.unsubscribeStore();
    clearTimeout(this.timer)

  }
}
