import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { CartModel } from 'src/app/models/cart.model';
import { OrdersModel } from 'src/app/models/orders.model';
import store from 'src/app/redux/store';
import { CartService } from 'src/app/services/cart.service';
import { OrderService } from 'src/app/services/order-service';
import { FormGroup } from '@angular/forms';
import { jsPDF } from 'jspdf';
import html2canvas from 'html2canvas';
import { UserModel } from 'src/app/models/user.model';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalService } from 'src/app/services/modal.service';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css'],
})
export class OrderComponent implements OnInit {
  @ViewChild('pdfTable', { static: false }) pdfTable: ElementRef;
  public items: CartModel[];
  public totalPrice: number;
  public quantity: number;
  public date = new Date().toLocaleDateString();
  public minDate: Date;
  public shipmentPrice: number;
  public order = new OrdersModel();
  public orderComplete: boolean;
  public cart: CartModel;
  public form: FormGroup;
  public download = false;
  public user: UserModel;
  public shippingDates: Date[];
  public duplicateArr: Array<any>;
  public cartStatus = store.getState().cartState.cart;
  public spinner: boolean = false;
  public searchInput: '';
  public timer: ReturnType<typeof setTimeout>;

  public year = Array.from(
    { length: 10 },
    (v, i) => new Date().getFullYear() - 1 + i + 1
  );
  public month = Array.from({ length: 12 }, (item, i) => {
    return new Date(1, i).getMonth() + 1;
  });

  requiredError = new FormControl('', [Validators.required]);
  creditCardError = new FormControl('', [
    Validators.required,
    Validators.pattern('0?[1-9][0-9]{14,16}$'),
  ]);

  creditCardCvv = new FormControl('', [
    Validators.required,
    Validators.pattern('[0-9]{3,}$'),
  ]);
  city = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);

  street = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);

  validMonth = new FormControl('', [Validators.required]);
  validYear = new FormControl('', [Validators.required]);

  id = new FormControl('', [
    Validators.required,
    Validators.pattern('0?[1-9][0-9]{7,8}$'),
  ]);

  getErrorMessage() {
    return this.requiredError.hasError('required') ? '  ' : '';
  }

  constructor(
    private myOrderService: OrderService,
    private myCartService: CartService,
    private myRouter: Router,
    private myModalService: ModalService
  ) {
    this.orderComplete = false;
    this.user = store.getState().authState.user;
    this.minDate = new Date();
  }

  public async ngOnInit(): Promise<void> {
    this.duplicateArr = [];
    this.shipmentPrice = 30;
    try {
      this.cartStatus = await this.myCartService.getCarId(this.user.userId);
      this.items = await this.myCartService.getCartProducts(
        this.cartStatus[0].cartId
      );
      if (this.items.length === 0) {
        this.myRouter.navigateByUrl('/shopping');
      }
    } catch (err) {
      console.log(err);
    }

    this.items = store.getState().cartState.cart;
    this.quantity = this.items.length;
    let sum = 0;
    for (let i = 0; i < this.items.length; i++) {
      sum += this.items[i].totalPrice;
      this.totalPrice = sum;
    }
    //get shipping dates
    this.shippingDates = await (
      await this.myOrderService.getShippingDates()
    ).map((m) => m.shippingDate);

    //check how many same shipping day exits
    const calculateCount = (shippingDates: Array<any>, query: Date) => {
      let count = 0;
      for (let i = 0; i < shippingDates.length; i++) {
        if (shippingDates[i] === query) {
          count++;
          continue;
        }
        if (Array.isArray(shippingDates[i])) {
          count += calculateCount(shippingDates[i], query);
        }
      }
      return count;
    };
    // push blocked dates to array
    for (let i = 0; i < this.shippingDates.length; i++) {
      const result = calculateCount(this.shippingDates, this.shippingDates[i]);
      if (result === 3) {
        this.duplicateArr.push(this.shippingDates[i]);
      }
    }
  }

  //date picker filter the blocks dates
  myFilter = (d: Date): boolean => {
    d = new Date(d);
    let date: any;
    let month: any;
    if (d.getDate().toString().length < 2) {
      date = '0' + d.getDate().toString();
    } else {
      date = d.getDate().toString();
    }

    if ((d.getMonth() + 1).toString().length < 2) {
      month = '0' + (d.getMonth() + 1).toString();
    } else {
      month = (d.getMonth() + 1).toString();
    }

    const day = d.getFullYear().toString() + '-' + month + '-' + date;
    if (this.duplicateArr) {
      return !this.duplicateArr.includes(day) && d.getDay() !== 6;
    }
    return null;
  };

  public async sendOrder(): Promise<void> {
    const cartId = store.getState().cartState.cart;
    const userId = store.getState().authState.user;
    this.order.finalPrice = +this.totalPrice + +this.shipmentPrice.toFixed(2);
    try {
      this.spinner = true;
      this.order.creditCard = this.order.creditCard
        .toString()
        .slice(this.order.creditCard.length - 4);
      await this.myOrderService.addOrder(
        cartId[0].cartId,
        userId.userId,
        this.order
      );
      await this.myCartService.changeCartStatus(cartId[0].cartId, 0, this.cart);
      await this.myCartService.emptyCart(cartId[0].cartId);

      this.timer = setTimeout(() => {
        this.spinner = false;
        this.orderComplete = true;
      }, 3000);
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal();
      }
      console.log(err);
    }
  }

  public downloadAsPDF(): void {
    this.download = true;
    const div = document.getElementById('billingTable');
    const options = {
      background: 'white',
      scale: 3,
    };

    html2canvas(div, options)
      .then((canvas) => {
        var img = canvas.toDataURL('image/PNG');
        var doc = new jsPDF('l', 'mm', 'a4');

        // Add image Canvas to PDF
        const bufferX = 5;
        const bufferY = 5;
        const imgProps = (<any>doc).getImageProperties(img);
        const pdfWidth = doc.internal.pageSize.getWidth() - 2 * bufferX;
        const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
        doc.addImage(
          img,
          'PNG',
          bufferX,
          bufferY,
          pdfWidth,
          pdfHeight,
          undefined,
          'FAST'
        );

        return doc;
      })
      .then((doc) => {
        doc.save(`${this.user.firstName}_ .pdf`);
        this.download = false;
      });
  }

  public fillOutCity() {
    this.user = store.getState().authState.user;
    const city = this.user.city;
    this.order.shippingCity = city;
  }

  public fillOutStreet() {
    this.user = store.getState().authState.user;
    const street = this.user.street;
    this.order.shippingStreet = street;
  }

  public search(event: KeyboardEvent): void {
    if (
      (event.keyCode > 47 && event.keyCode < 58) ||
      (event.keyCode > 64 && event.keyCode < 91) ||
      (event.keyCode > 95 && event.keyCode < 112)
    ) {
      const results = this.items.filter((s) =>
        s.productName.toString().includes(this.searchInput)
      );
      this.items = results;
      if (results.length > 0) {
        document.getElementById('name').style.backgroundColor = 'yellow';
      } else {
        if (document.getElementById('name')) {
          document.getElementById('name').style.backgroundColor = '#fff';
        }
      }
    } else {
      this.items = store.getState().cartState.cart;
      if (document.getElementById('name')) {
        document.getElementById('name').style.backgroundColor = '#fff';
      }
    }
  }

  public ngOnDestroy(): void {
    clearTimeout(this.timer);
  }
}
