import { Component, OnInit } from '@angular/core';
import store from 'src/app/redux/store';
import { Unsubscribe } from 'redux';
import { CartModel } from 'src/app/models/cart.model';
import { OrdersModel } from 'src/app/models/orders.model';
import { OrderService } from 'src/app/services/order-service';

@Component({
  selector: 'app-shopping-main',
  templateUrl: './shopping-main.component.html',
  styleUrls: ['./shopping-main.component.css'],
})
export class ShoppingMainComponent implements OnInit {
  public user = store.getState().authState.user;
  public isAdmin: boolean;
  public cartLength: number;
  public items: CartModel[];
  public unsubscribeStore: Unsubscribe;
  public totalPrice: number;
  public lastOrder: OrdersModel[];
  public lastOrderDate: string;

  public showFiller = false;
  public constructor(private myOrderService: OrderService) {}

  public async ngOnInit(): Promise<void> {
    try {
      this.lastOrder = await this.myOrderService.getOrderDates(
        this.user.userId
      );
      if (this.lastOrder && this.lastOrder.length !== 0) {
        this.lastOrderDate = new Date(
          this.lastOrder[0].orderDate
        ).toLocaleDateString();
      }
    } catch (err) {
      console.log(err);
    }
    this.unsubscribeStore = store.subscribe(() => {
      this.cartLength = store.getState().cartState.cart.length;
      this.items = store.getState().cartState.cart;
      let sum = 0;
      if (this.items.length === 0) {
        this.totalPrice = 0;
      } else {
        for (let i = 0; i < this.items.length; i++) {
          sum += this.items[i].totalPrice;
          this.totalPrice = sum;
        }
      }
    });

    if (this.user.isAdmin) {
      this.isAdmin = true;
    }
  }
}
