import { HttpClient } from '@angular/common/http';
import { Component, Input, OnInit } from '@angular/core';
import { CartModel } from 'src/app/models/cart.model';
import { CartActionType } from 'src/app/redux/cart-state';
import store from 'src/app/redux/store';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-cart-card',
  templateUrl: './cart-card.component.html',
  styleUrls: ['./cart-card.component.css'],
})
export class CartCardComponent implements OnInit {
  @Input()
  public items: CartModel;
  public imageUrl: string;
  public isValid: boolean = true;
  public category: string = ""
  public categoryPrice: string = ""
  public cartId = JSON.parse(sessionStorage.getItem('cartId'));
  public user = store.getState().authState.user;
  public timer: ReturnType<typeof setTimeout>;

  constructor(
    private myHttp: HttpClient,
    private myCartService: CartService,
    private myModalService:ModalService
  ) {}

  public async ngOnInit():Promise<void> {
    if(this.items && this.items.categoryId === 5){
        this.category = ""
        this.categoryPrice =""
    }
 
    this.imageUrl =
      environment.productsUrl + 'images/' + this.items.imageFileName;
    if (this.cartId == null) {
      try {
        this.cartId = await this.myCartService.getCarId(this.user.userId);
       
      } catch (err) {
        if (err.status === 403) {
            this.myModalService.openSessionOverModal()
        }
        console.log(err);
      }
    }
  }
  public async deleteProduct():Promise<void> {
    const productId = this.items.productId;
    try {
      await this.myHttp
        .delete<CartModel>(
          environment.customerUrl + this.cartId[0].cartId + '/' + productId
        )
        .toPromise();
      store.dispatch({ type: CartActionType.CartDeleted, payload: productId });
      await this.myCartService.changeCartStatus(
        this.cartId[0].cartId,
        0,
        this.items
      );
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal()
    }
      console.log(err);
    }
  }

  public async changeQuantity():Promise<void> {
    try {
      await this.myCartService.changeQuantityCart(
        this.items,
        this.cartId[0].cartId,
        this.items.productId
      );
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal()
    }
      console.log(err);
    }
  }

  public addQuantity():void {
    if (this.items.quantity < 100) {
      this.items.quantity++;
      this.isValid = true;
      this.changeQuantity();
    }
    else {
        this.isValid = false;
      }
  }

  public removeQuantity():void {
    if (this.items.quantity > 1) {
      this.items.quantity--;
      this.isValid = true;
      this.changeQuantity();
    } else {
      this.isValid = false;
    }
    this.timer = setTimeout(() => {
      this.isValid = true;
    }, 2000);
  }

  public ngOnDestroy(): void {
    clearTimeout(this.timer);
  }
}
