import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './auth-area/login/login.component';
import { RegisterComponent } from './auth-area/register/register.component';
import { OrderComponent } from './orders-area/order/order.component';
import { LoginGuard } from './services/login.guard';
import { ShoppingMainComponent } from './shopping-area/shopping-main/shopping-main.component';

const routes: Routes = [
    { path: "home", component: LoginComponent },
    { path: "register", component: RegisterComponent },
    { path: "shopping",canActivate:[LoginGuard], component: ShoppingMainComponent },
    { path: "order",canActivate:[LoginGuard], component: OrderComponent },
    { path: "", redirectTo: "/home", pathMatch: "full" }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
