import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { OrdersModel } from '../models/orders.model';

@Injectable({
  providedIn: 'root',
})
export class OrderService {
  public constructor(private myHttp: HttpClient) {}

  //add new order
  public async addOrder(cartId:number, userId:number,order: OrdersModel): Promise<OrdersModel> {
    const addedOrder = await this.myHttp
      .post<OrdersModel>(environment.ordersUrl +cartId +"/" + userId, order)
      .toPromise();
    return addedOrder;
  }

  //get shipping dates
  public async getShippingDates(): Promise<OrdersModel[]> {
    const shippingDates = await this.myHttp
      .get<OrdersModel[]>(environment.ordersUrl)
      .toPromise();
    return shippingDates;
  }

  //get order date by user
  public async getOrderDates(userId:number): Promise<OrdersModel[]> {
    const orderDate = await this.myHttp
      .get<OrdersModel[]>(environment.ordersUrl +"last-order/" + userId)
      .toPromise();
    return orderDate;
  }
  
}
