import { Injectable } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { AdminModalComponent } from '../shared-area/admin-modal/admin-modal.component';
import { LastCartComponent } from '../shared-area/last-cart/last-cart.component';
import { LogoutModalComponent } from '../shared-area/logout-modal/logout-modal.component';
import { SessionModalComponent } from '../shared-area/session-modal/session-modal.component';

@Injectable({
  providedIn: 'root'
})
export class ModalService {

    constructor(public matDialog: MatDialog) { }
    openLogOutModal() {
        const dialogConfig = new MatDialogConfig();
        dialogConfig.disableClose = false;
        dialogConfig.id = "logout-button-holder";
        dialogConfig.height = "250px";
        dialogConfig.width = "600px";
      this.matDialog.open(LogoutModalComponent, dialogConfig);
      }

      openSessionOverModal() {
        const dialogConfig = new MatDialogConfig();
        dialogConfig.disableClose = true;
        dialogConfig.height = "300px";
        dialogConfig.width = "600px";
      this.matDialog.open(SessionModalComponent, dialogConfig);
      }

      openLastCartOverModal() {
        const dialogConfig = new MatDialogConfig();
        dialogConfig.disableClose = true;
        dialogConfig.height = "350px";
        dialogConfig.width = "600px";
      this.matDialog.open(LastCartComponent, dialogConfig);
      }

      openAdminOverModal() {
        const dialogConfig = new MatDialogConfig();
        dialogConfig.disableClose = true;
        dialogConfig.height = "350px";
        dialogConfig.width = "600px";
      this.matDialog.open(AdminModalComponent, dialogConfig);
      }
      closeModal(){
         this.matDialog.closeAll() 
      }
      
}


