import { CredentialsModel } from '../models/credentials.model';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { UserModel } from '../models/user.model';
import { environment } from 'src/environments/environment';
import store from '../redux/store';
import {
  userLoggedInAction,
  userLoggedOutAction,
  userRegisteredAction,
} from '../redux/auth-state';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  public constructor(private myHttp: HttpClient ,private myRouter: Router,) {}

  //register
  public async register(user: {}): Promise<UserModel> {
    const RegisteredInUser = await this.myHttp
      .post<UserModel>(environment.registerUrl, user)
      .toPromise();
    store.dispatch(userRegisteredAction(RegisteredInUser));
    return RegisteredInUser;
  }

  //check duplicate user or id
  public async checkDuplicate(value: string): Promise<boolean> {
    const IsDuplicate = await this.myHttp
      .get<boolean>(environment.duplicateUrl + value)
      .toPromise();
    return IsDuplicate;
  }

  // login
  public async login(credentials: CredentialsModel): Promise<UserModel> {
    const loggedInUser = await this.myHttp
      .post<UserModel>(environment.loginUrl, credentials)
      .toPromise();
    store.dispatch(userLoggedInAction(loggedInUser));
    return loggedInUser;
  }

  //logout
  public logout(): void {
    store.dispatch(userLoggedOutAction());
  }
}
