import { Component, Input, OnInit } from '@angular/core';
import { Unsubscribe } from 'redux';
import { CartModel } from 'src/app/models/cart.model';
import { ProductModel } from 'src/app/models/product.model';
import { ProductsActionType } from 'src/app/redux/products-state';
import store from 'src/app/redux/store';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-product-card',
  templateUrl: './product-card.component.html',
  styleUrls: ['./product-card.component.css'],
})
export class ProductCardComponent implements OnInit {
  @Input()
  public product: ProductModel;
  public imageUrl: string;
  public cartInfo: CartModel;
  public isEdit: boolean;
  public quantity: number;
  public isValidMin: boolean = true;
  public isInCart: boolean = false;
  public isUpdated: boolean = false;
  public isChanged: boolean = false;
  public unsubscribeStore: Unsubscribe;
  public user = store.getState().authState.user;
  public IsAdmin: boolean;
  public cartId = JSON.parse(sessionStorage.getItem('cartId'));
  public timer: ReturnType<typeof setTimeout>;
  constructor(
    private myCartService: CartService,
    private myModalService: ModalService
  ) {}

  public async ngOnInit(): Promise<void> {
    if (this.user.isAdmin) {
      this.IsAdmin = true;
    }
    this.product.quantity = 1;
    this.imageUrl =
      environment.productsUrl + 'images/' + this.product.imageFileName;
    //get if this product in cart onload
    this.checkIfInCart();

    //get if this product in cart after change
    this.unsubscribeStore = store.subscribe(() => {
      this.checkIfInCart();
    });
  }

  public checkIfInCart() {
    const cartProduct = store
      .getState()
      .cartState.cart.find((p) => p.productId === this.product.productId);
    if (!cartProduct) {
      this.isEdit = false;
      this.product.quantity = 1;
    } else {
      this.isEdit = true;
      this.product.quantity = cartProduct.quantity;
      this.quantity = this.product.quantity;
    }
  }

  //add to cart function
  public async addToCart(): Promise<void> {
    if (this.product.quantity >= 1 && this.product.quantity <= 100) {
        this.isChanged = false
      try {
        await this.myCartService.addToCart(this.product, this.cartId[0].cartId);
        if (store.getState().cartState.cart.length === 1) {
          await this.myCartService.changeCartStatus(
            this.cartId[0].cartId,
            1,
            this.cartInfo
          );
          this.isInCart = true;
          this.isEdit = true;
         
        }
      } catch (err) {
        if (err.status === 403) {
          this.myModalService.openSessionOverModal();
        }
        console.log(err.message);
    }  

    } else {
      this.isValidMin = false;
      this.product.quantity = 1;
    }
    this.timer = setTimeout(() => {
      this.isInCart = false;
    }, 2000);
  }

  //change product quantity
  public async changeQuantity(): Promise<void> {
    try {
      if (this.product.quantity >= 1  && this.product.quantity <= 100) {
        this.isValidMin = true;
        this.cartId = await this.myCartService.getCarId(this.user.userId);
        await this.myCartService.changeQuantity(
          this.product,
          this.cartId[0].cartId,
          this.product.productId
        );
        this.isUpdated = true;
        this.isChanged = false

      } else {
        this.product.quantity = this.quantity;
        this.isValidMin = false;
        this.isChanged = false
      }
      this.timer = setTimeout(() => {
        this.isUpdated = false;
      }, 2000);
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal();
      }
      console.log(err.message);
    }
  }

  //add quantity button
  public addQuantity(): void {
    if (this.product.quantity < 100) {
      this.product.quantity++;
      this.isValidMin = true;
      this.isChanged = true
    }
    else {
        this.isValidMin = false;
      
      }
  }

  //remove quantity button
  public removeQuantity(): void {
    if (this.product.quantity > 1) {
      this.product.quantity--;
      this.isValidMin = true;
      this.isChanged = true
    } else {
      this.isValidMin = false;
      
    }
    this.timer = setTimeout(() => {
      this.isValidMin = true;
    }, 2000);
  }

  //Admin only !!! edit product
  public editProduct(): void {
    const newProduct = { ...this.product };
    store.dispatch({
      type: ProductsActionType.ProductToUpdate,
      payload: newProduct,
    });
  }
  public ngOnDestroy(): void {
    this.unsubscribeStore && this.unsubscribeStore();
    clearTimeout(this.timer);
  }

  public inputChange(){
      this.isChanged = true
  }
}
