import { AuthService } from './../../services/auth.service';
import { Component } from '@angular/core';
import { CredentialsModel } from '../../models/credentials.model';
import { Router } from '@angular/router';
import { CartService } from 'src/app/services/cart.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModalService } from 'src/app/services/modal.service';
import store from 'src/app/redux/store';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent {
  public credentials = new CredentialsModel();
  public errorMsg: string;
  public hide = true;
  public form: FormGroup;

  public constructor(
    private myAuthService: AuthService,
    private myCartService: CartService,
    private myRouter: Router,
    private myModalService:ModalService
  ) {}

  requiredError = new FormControl('', [Validators.required]);
  username = new FormControl('', [
    Validators.required,
    Validators.email
  ]);
  password = new FormControl('', [
    Validators.required
  ]);

  getErrorMessage() {
    return this.requiredError.hasError('required') ? '  ' : '';
  }
  public async login():Promise<void> {
    try {
      await this.myAuthService.login(this.credentials);
      const user = store.getState().authState.user
      const cartStatus = await this.myCartService.getCarId(user.userId);
      if (cartStatus[0].cart_status === 0) {
        this.myRouter.navigateByUrl('/shopping');
      } else  {
          this.myModalService.openLastCartOverModal()
      }
    } catch (err) {
      this.errorMsg = err.error;
    }
  }



  
}
