import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CartModel } from 'src/app/models/cart.model';
import store from 'src/app/redux/store';
import { CartService } from 'src/app/services/cart.service';

@Component({
  selector: 'app-last-cart',
  templateUrl: './last-cart.component.html',
  styleUrls: ['./last-cart.component.css'],
})
export class LastCartComponent implements OnInit {
  public user = store.getState().authState.user;
  public date: string;
  public items: CartModel[];
  public totalPrice: number;
  public totalPriceArr: number[];
  public cart: CartModel;

  constructor(
    private myCartService: CartService,
    private myRouter: Router,
    public matDialog: MatDialog
  ) {}

  public async ngOnInit(): Promise<void> {
    const cartStatus = await this.myCartService.getCarId(this.user.userId);
    try {
      this.items = await this.myCartService.getCartProducts(
        cartStatus[0].cartId
      );
      this.totalPriceArr = this.items.map((p) => p.totalPrice);
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.totalPriceArr[i];
        this.totalPrice = sum;
      }
      this.date = new Date(cartStatus[0].date).toLocaleDateString();
    } catch (err) {
      console.log(err);
    }
  }

  public async emptyCart(): Promise<void> {
    try {
      const cartStatus = await this.myCartService.getCarId(this.user.userId);
      await this.myCartService.emptyCart(cartStatus[0].cartId);
      this.matDialog.closeAll()
      this.myRouter.navigateByUrl('/shopping');
    } catch (err) {
      console.log(err);
    }
  }

  public async changeCartStatus(cart_status: number): Promise<void> {
    try {
      const cartStatus = await this.myCartService.getCarId(this.user.userId);
      await this.myCartService.changeCartStatus(
        cartStatus[0].cartId,
        cart_status,
        this.cart
      );
        this.matDialog.closeAll()
      this.myRouter.navigateByUrl('/shopping');
    } catch (err) {
      console.log(err);
    }
  }
}
