import { Component, OnInit } from '@angular/core';
import { Unsubscribe } from 'redux';
import { CartModel } from 'src/app/models/cart.model';
import store from 'src/app/redux/store';
import { CartService } from 'src/app/services/cart.service';
import { ModalService } from 'src/app/services/modal.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css'],
})
export class CartComponent implements OnInit {
  public price: number;
  public totalPrice: number;
  public quantity: number;
  public items: CartModel[];
  public isEmpty: boolean;
  public cartEmpty: boolean;
  public unsubscribeStore: Unsubscribe;
  public shippingPrice: number;
  public cart: CartModel;
  public isAdmin: boolean;
  public cartId: CartModel[];
  public user = store.getState().authState.user;
  constructor(
    private myCartService: CartService,
    private myModalService: ModalService
  ) {}

  public async ngOnInit(): Promise<void> {
    if (this.user.isAdmin) {
      this.isAdmin = true;
    }
    // get cart id
    try {
      this.cartId = await this.myCartService.getCarId(this.user.userId);
      this.items = await this.myCartService.getCartProducts(
        this.cartId[0].cartId
      );
     
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal();
      }
      this.items = [];
      console.log(err);
    }

    //case cart is empty
    if (this.items.length === 0) {
      this.isEmpty = true;
      this.totalPrice = 0.0;
      this.quantity = 0;
    }

    this.calculateTotal();

    this.shippingPrice = 30;
    this.unsubscribeStore = store.subscribe(() => {
      this.items = store.getState().cartState.cart;
      this.calculateTotal();
    });
  }

  public calculateTotal() {
    this.quantity = this.items.length;
    let sum = 0;
    if (this.items.length === 0) {
      this.totalPrice = 0;
      this.isEmpty = true;
    } else {
      this.isEmpty = false;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].totalPrice;
        this.totalPrice = sum;
      }
    }
  }

  public emptyCartMessage(): void {
    this.cartEmpty = true;
  }
  public cancel(): void {
    this.cartEmpty = false;
  }

  public async emptyCart(): Promise<void> {
    try {
      await this.myCartService.emptyCart(this.cartId[0].cartId);
      this.cartEmpty = false;
      this.quantity = 0;
      this.totalPrice = 0.0;
      this.items.length = 0;
      this.isEmpty = true;
      await this.myCartService.changeCartStatus(
        this.cartId[0].cartId,
        0,
        this.cart
      );

      //   this.isEmpty = true
    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal();
      }
      console.log(err);
    }
  }
  public ngOnDestroy(): void {
    this.unsubscribeStore && this.unsubscribeStore();
  }
}
