import { CartModel } from '../models/cart.model';

export class CartState {
  public cart: CartModel[] = [];
  public constructor() {
    const cart = JSON.parse(sessionStorage.getItem('cart'));
    if (cart) {
      this.cart = cart;
    }
  }
}

export enum CartActionType {
  CartDownloaded = 'CartDownloaded',
  CartAdded = 'CartAdded',
  CartUpdated = 'CartUpdated',
  CartDeleted = 'CartDeleted',
  AllCartDeleted = 'AllCartDeleted',
  CartIdDownloaded = 'CartIdDownloaded',
}

export interface CartAction {
  type: CartActionType;
  payload?: any;
}

export function cartReducer(
  currentState: CartState = new CartState(),
  action: CartAction
): CartState {
  const newState = { ...currentState };

  switch (action.type) {
    case CartActionType.CartDownloaded:
      newState.cart = action.payload;
      sessionStorage.setItem('cart', JSON.stringify(newState.cart));
      break;

    case CartActionType.CartIdDownloaded:
      newState.cart = action.payload;
      sessionStorage.setItem('cartId', JSON.stringify(newState.cart));
      break;

    case CartActionType.CartAdded:
      newState.cart.push(action.payload);
      break;

    case CartActionType.CartUpdated:
      const indexToUpdate = newState.cart.findIndex(
        (c) => c.cartId == action.payload.id
      );
      newState.cart[indexToUpdate] = action.payload;
      break;

    case CartActionType.CartDeleted:
      const indexToDelete = newState.cart.findIndex(
        (c) => c.productId == action.payload
      );
      newState.cart.splice(indexToDelete, 1);
      break;

    case CartActionType.AllCartDeleted:
      newState.cart = [];
      break;
  }

  return newState; // Return the newState.
}
