import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { CartModel } from '../models/cart.model';
import { ProductModel } from '../models/product.model';
import { CartActionType } from '../redux/cart-state';
import store from '../redux/store';

@Injectable({
  providedIn: 'root',
})
export class CartService {
  public constructor(private myHttp: HttpClient) {}

  //get cart details
  public async getCarId(userId: number): Promise<CartModel[]> {
    const result = await this.myHttp
      .get<CartModel[]>(environment.customerUrl + 'cartId/' + userId)
      .toPromise();
    sessionStorage.setItem('cartId', JSON.stringify(result));
    return result;
  }

  //change cart status 0 or 1
  public async changeCartStatus(
    cartId: number,
    cart_status: number,
    cart: CartModel
  ): Promise<void> {
    await this.myHttp
      .put<CartModel>(
        environment.customerUrl + 'cart-status/' + cartId + '/' + cart_status,
        cart
      )
      .toPromise();
  }

  //change product item quantity
  public async changeQuantity(
    product: ProductModel,
    cartId: number,
    productId: number
  ): Promise<void> {
    const updatedQuantity = await this.myHttp
      .put<CartModel>(
        environment.customerUrl + 'update/' + cartId + '/' + productId,
        product
      )
      .toPromise();
    store.dispatch({
      type: CartActionType.CartUpdated,
      payload: updatedQuantity,
    });
    this.getCartProducts(cartId);
  }


  //change cart item quantity
  public async changeQuantityCart(
    product: CartModel,
    cartId: number,
    productId: number
  ): Promise<void> {
    const updatedQuantity = await this.myHttp
      .put<CartModel>(
        environment.customerUrl + 'update/' + cartId + '/' + productId,
        product
      )
      .toPromise();
    store.dispatch({
      type: CartActionType.CartUpdated,
      payload: updatedQuantity,
    });
    this.getCartProducts(cartId);
  }

  //delete all cart items
  public async emptyCart(cartId: number): Promise<void> {
    const result = await this.myHttp
      .delete<CartModel>(environment.customerUrl + 'empty-cart/' + cartId)
      .toPromise();
    store.dispatch({ type: CartActionType.AllCartDeleted, payload: result });
  }


  //add to cart
  public async addToCart(
    product: ProductModel,
    cartId: number
  ): Promise<ProductModel> {
    const addedProduct = await this.myHttp
      .post<ProductModel>(environment.customerUrl + cartId, product)
      .toPromise();
    store.dispatch({ type: CartActionType.CartAdded, payload: addedProduct });
    return addedProduct;
  }
  public async getCartProducts(cartId: number): Promise<CartModel[]> {
    const cart = await this.myHttp
      .get<CartModel[]>(environment.customerUrl + cartId)
      .toPromise();
    store.dispatch({ type: CartActionType.CartDownloaded, payload: cart });
    return cart;
  }

  //delete from cart
  public async deleteCartProduct(
    cartId: number,
    productId: number
  ): Promise<CartModel> {
    const result = await this.myHttp
      .delete<CartModel>(environment.customerUrl + cartId + '/' + productId)
      .toPromise();
    store.dispatch({ type: CartActionType.CartDeleted, payload: result });
    return result;
  }
}
