import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { ProductModel } from '../models/product.model';
import { ProductsActionType } from '../redux/products-state';
import store from '../redux/store';

@Injectable({
  providedIn: 'root',
})
export class ProductsService {
  public constructor(private myHttp: HttpClient) {}

  //get all products
  public async getAllProducts(): Promise<ProductModel[]> {
    const products = await this.myHttp
      .get<ProductModel[]>(environment.productsUrl)
      .toPromise();
    store.dispatch({
      type: ProductsActionType.ProductsDownloaded,
      payload: products,
    });

    return store.getState().productsState.products;
  }

  //get all categories
  public async getAllCategories(): Promise<ProductModel[]> {
    const categories = await this.myHttp
      .get<ProductModel[]>(environment.productsUrl + '/categories')
      .toPromise();
    return categories;
  }

  //get products by category
  public async getAllProductsByCategory(
    categoryId: number
  ): Promise<ProductModel[]> {
    const products = await this.myHttp
      .get<ProductModel[]>(environment.productsUrl + 'category/' + categoryId)
      .toPromise();
    return products;
  }

  //add product - admin only!
  public async addProduct(product: ProductModel): Promise<ProductModel> {
    const myFormData = new FormData();
    myFormData.append('productName', product.productName);
    myFormData.append('categoryId', product.categoryId.toString());
    myFormData.append('price', product.price.toString());
    myFormData.append('image', product.image);
    myFormData.append('imageFileName', product.image.name);
    const addedProduct = await this.myHttp
      .post<ProductModel>(environment.productsUrl, myFormData)
      .toPromise();
    store.dispatch({
      type: ProductsActionType.ProductAdded,
      payload: addedProduct,
    });
    return addedProduct;
  }

  //edit product - admin only!
  public async editProduct(product: ProductModel): Promise<ProductModel> {
    const myFormData = new FormData();
    myFormData.append('productName', product.productName);
    myFormData.append('categoryId', product.categoryId.toString());
    myFormData.append('price', product.price.toString());
    if (product.image != null) {
      myFormData.append('image', product.image);
      myFormData.append('imageFileName', product.image.name);
    } else {
      myFormData.append('imageFileName', product.imageFileName);
    }

    const updatedProduct = await this.myHttp
      .put<ProductModel>(
        environment.productsUrl + product.productId,
        myFormData
      )
      .toPromise();
    store.dispatch({
      type: ProductsActionType.ProductUpdated,
      payload: updatedProduct,
    });
    return updatedProduct;
  }
}
