import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserModel } from 'src/app/models/user.model';
import { AuthService } from 'src/app/services/auth.service';
import {
  AbstractControl,
  FormBuilder,
  FormControl,
  FormGroup,
  FormGroupDirective,
  NgForm,
  Validators,
} from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ErrorStateMatcher } from '@angular/material/core';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(
    control: FormControl | null,
    form: FormGroupDirective | NgForm | null
  ): boolean {
    const isSubmitted = form && form.submitted;
    return !!(
      control &&
      control.invalid &&
      (control.dirty || control.touched || isSubmitted)
    );
  }
}

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
})
export class RegisterComponent implements OnInit {
  idFormCtrl = new FormControl('', [
    Validators.required,
    Validators.pattern('0?[1-9][0-9]{7,8}$'),
  ]);

  firstName = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);

  lastName = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);

  city = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);

  street = new FormControl('', [
    Validators.required,
    Validators.pattern('^[-]|[A-Z]|[a-z]{2,}'),
  ]);


  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);

  passwordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(6),
    Validators.pattern(
      '^(?=.*[a-z])|(?=.*[A-Z])|(?=.*[-])(?=.*[0-9])(?=.*[^w*])[^s]{6,}$'
    ),
  ]);

  passwordConfirmFormControl = new FormControl('', [Validators.required]);

  matcher = new MyErrorStateMatcher();
  public user = new UserModel();
  public myControl = new FormControl();
  public options: string[] = [
    '',
    '',
    '',
    ' ',
    ' ',
    ' ',
    '',
    '',
    '-',
    '',
    ' ',
    ' ',
  ];
  public filteredOptions: Observable<string[]>;
  public isLinear = false;
  public duplicateIdErr: boolean;
  public duplicateUserErr: boolean;
  public passwordConfirmErr: boolean;
  public formGroup: FormGroup;
  public newArr: {};
  get formArray(): AbstractControl | null {
    return this.formGroup.get('formArray');
  }

  public constructor(
    private myAuthService: AuthService,
    private myRouter: Router,
    private _formBuilder: FormBuilder
  ) {}

  public ngOnInit(): void {
    this.formGroup = this._formBuilder.group({
      formArray: this._formBuilder.array([
        this._formBuilder.group({
          id: this.idFormCtrl,
          username: this.emailFormControl,
          password: this.passwordFormControl,
          passwordConfirm: ['', Validators.nullValidator],
        }),
        this._formBuilder.group({
          firstName: this.firstName,
          lastName: this.lastName,
          city: ['', Validators.required],
          street: this.street,
        }),
      ]),
    });

    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map((value) => this._filter(value))
    );
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.options.filter((option) =>
      option.toLowerCase().includes(filterValue)
    );
  }

  public async duplicateId(event: KeyboardEvent): Promise<void> {
    try {
      if (event.keyCode !== 8) {
        this.duplicateIdErr = await this.myAuthService.checkDuplicate(
          this.user.id
        );
      }
    } catch (err) {
      console.log(err);
    }
  }

  public passwordConfirm(): void {
    if (this.user.password !== this.user.passwordValidation) {
      this.passwordConfirmErr = true;
    } else {
      this.passwordConfirmErr = false;
    }
  }

  public async duplicateUser(event: KeyboardEvent): Promise<void> {
    try {
      if (
        (event.keyCode > 47 && event.keyCode < 58) ||
        (event.keyCode > 64 && event.keyCode < 91) ||
        (event.keyCode > 95 && event.keyCode < 112)
      ) {
        this.duplicateUserErr = await this.myAuthService.checkDuplicate(
          this.user.username
        );
      }
    } catch (err) {
      console.log(err);
    }
  }

  public async register(): Promise<void> {
    this.newArr = {
      ...this.formArray.value[0],
      ...this.formArray.value[1],
    };
    const registerArray = [];
    registerArray.push(this.newArr);

    try {
      await this.myAuthService.register(this.newArr);
      this.myRouter.navigateByUrl('/shopping');
    } catch (err) {
      console.log(err);
    }
  }
}
