import { Component, OnInit } from '@angular/core';
import { ProductModel } from 'src/app/models/product.model';
import store from 'src/app/redux/store';
import { ProductsService } from 'src/app/services/product.service';
import { ModalService } from 'src/app/services/modal.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css'],
})
export class ProductsComponent implements OnInit {
  public products: ProductModel[];
  public categories: ProductModel[]
  public searchBox: string;
  public clearSearch: boolean;
  public emptyResult : boolean

  constructor(
    private myProductsService: ProductsService,
    private myModalService:ModalService

  ) {}

  public async ngOnInit():Promise<void> {
    try {
      this.products = await this.myProductsService.getAllProducts();
      this.categories = await this.myProductsService.getAllCategories()

    } catch (err) {
      if (err.status === 403) {
        this.myModalService.openSessionOverModal()
    }
      console.log(err);
    }

  }

  //category filter
  public filterAllProducts():void {
    this.searchBox = '';
    this.clearSearch = false;
    this.products = store.getState().productsState.products;
    this.emptyResult = false
  }

  public async getItemsByCategory(categoryId: number):Promise<void>{
    this.products = store
      .getState()
      .productsState.products.filter((p) => p.categoryId.toString() === categoryId.toString());
  }

  public search(event: KeyboardEvent):void {
    const products = store.getState().productsState.products;
    this.clearSearch = true;
    if (event.keyCode !== 8) {
      const results = products.filter((s) =>
        s.productName.includes(this.searchBox)
      );
      this.products = results;
      if(results.length === 0){
          this.emptyResult = true
      }
      
    } else {
      this.products = store.getState().productsState.products;
      this.clearSearch = false;
      this.emptyResult = false
    }
    
  }

}
